* {
  margin: 0;
  padding: 0;
  scroll-behavior: smooth;
}

.App {
  font-family: "Quicksand";
}

.landing-page {
  background: linear-gradient(
      to bottom right,
      rgba(177, 252, 190, 0.6),
      rgba(82, 208, 255, 0.6),
      rgba(255, 162, 156, 0.6)
    ),
    url("https://images.unsplash.com/photo-1437914983566-976d85602771?ixlib=rb-1.2.1&ixid=eyJhcHBfaWQiOjEyMDd9&auto=format&fit=crop&w=1950&q=80");
  background-size: cover;
  background-position: center center;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-justify-content: center;
          justify-content: center;
  -webkit-align-items: center;
          align-items: center;
  font-weight: bold;
  height: 100vh;
}

.nav-arrow {
  color: rgb(177, 252, 190);
  -webkit-animation-duration: 2s;
          animation-duration: 2s;
  -webkit-animation-iteration-count: infinite;
          animation-iteration-count: infinite;
  -webkit-animation-name: bounce;
          animation-name: bounce;
  bottom: 10px;
  position: absolute;
}
.nav-arrow:hover {
  color: rgb(116, 194, 129);
}
@-webkit-keyframes bounce {
  0% {
    -webkit-transform: translateY(0);
            transform: translateY(0);
  }
  50% {
    -webkit-transform: translateY(-10px);
            transform: translateY(-10px);
  }
  100% {
    -webkit-transform: translateY(0);
            transform: translateY(0);
  }
}
@keyframes bounce {
  0% {
    -webkit-transform: translateY(0);
            transform: translateY(0);
  }
  50% {
    -webkit-transform: translateY(-10px);
            transform: translateY(-10px);
  }
  100% {
    -webkit-transform: translateY(0);
            transform: translateY(0);
  }
}

.app-flex-title {
  font-weight: 900;
}

.giv-title {
  font-size: 7em;
  color: #fff;
  text-align: center;
}

.giv-slogan {
  margin: 0 0 200px 0;
  font-size: 3rem;
}

@media screen and (max-width: 600px) {
  .landing-page {
    background-position: center center;
  }

  .giv-slogan {
    display: none;
  }
}

.footer-wrapper {
  background-color: #ffa29c;
  color: white;
  text-align: center;
  font-weight: bold;
  font-size: 1.5rem;
}

.footer {
  padding: 2%;
}

.copyright {
  color: white;
  font-size: 0.5rem;
  font-family: Roboto;
}

a + a {
  margin-left: 2%;
}

.icons {
  padding-top: 1%;
  padding-bottom: 1%;
}

.icon {
  text-decoration: none;
  border-bottom: none;
  position: relative;
  color: white;
}

.icon:hover {
  color: #67dafae7;
}

.icon-size {
  size: 2rem;
}

@media screen and (max-width: 767px) {
  /*768px is the difference between phone and tablet generally*/
  .footer-wrapper {
    font-size: 1.3rem;
  }

  .icon-size {
    size: 1.5rem;
  }
}

.partner-wrapper {
  background-color: #fff;
  color: #000000;
  font-weight: lighter;
  text-align: center;
  font-size: 1.5rem;
}

.partner-wrapper h1 {
  padding: 2%;
}

.images {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: row;
          flex-direction: row;
  -webkit-justify-content: center;
          justify-content: center;
  -webkit-align-items: center;
          align-items: center;
}

.container {
  position: relative;
  width: 50%;
}

.image {
  display: block;
  width: 100%;
  height: auto;
}

.overlay-left {
  position: absolute;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  height: 100%;
  width: 100%;
  opacity: 0.4;
  transition: 0.5s ease;
  background-color: rgb(177, 252, 190);
}

.overlay-right {
  position: absolute;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  height: 100%;
  width: 100%;
  opacity: 0.4;
  transition: 0.5s ease;
  background-color: rgb(82, 208, 255);
}

.text-bottom-left {
  color: #fff;
  font-weight: bold;
  font-size: 1.4rem;
  position: absolute;
  bottom: 0%;
  left: 5%;
  text-align: left;
}

.text-bottom-left .lighter {
  font-weight: lighter;
  font-size: 2.5rem;
}

.text-top-right {
  color: #fff;
  font-weight: bold;
  font-size: 1.4rem;
  position: absolute;
  text-align: right;
  top: 0%;
  right: 5%;
}

.text-top-right .lighter {
  font-weight: lighter;
  font-size: 2.5rem;
}

@media screen and (max-width: 767px) {
  /*768px is the difference between phone and tablet generally*/
  .images {
    -webkit-flex-direction: column;
            flex-direction: column;
  }

  .container img {
    border-radius: 10px;
  }

  .container {
    margin: 15px 0;
    width: 90%;
  }

  .overlay-left {
    border-radius: 10px;
  }

  .overlay-right {
    border-radius: 10px;
  }
}

.why-page {
  background: #b1fcbe;
}

.why-content {
  padding: 40px;
}

.why-title {
  font-size: 3rem;
  margin-bottom: 20px;
  text-align: center;
}

.why-cards {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: row;
          flex-direction: row;
  -webkit-justify-content: space-evenly;
          justify-content: space-evenly;
}

.why-card {
  background: #fff;
  box-shadow: 0 14px 28px rgba(0, 0, 0, 0.25), 0 10px 10px rgba(0, 0, 0, 0.22);
  border-radius: 6px;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-align-items: center;
          align-items: center;
  height: 340px;
  width: 25%;
  padding: 20px;
  opacity: 0.9;
}

.why-card:hover {
  opacity: 1;
  -webkit-transform: scale(1.02);
          transform: scale(1.02);
}

.card-title {
  font-size: 2em;
  text-align: center;
  margin: 5px 0 5px 0;
}

.card-img {
  -webkit-align-self: center;
          align-self: center;
  margin: 0 0 10px 0;
}

.card-description {
  -webkit-align-self: center;
          align-self: center;
  overflow: auto;
}

@media screen and (max-width: 767px) {
  /*768px is the difference between phone and tablet generally*/
  .why-cards {
    -webkit-flex-direction: column;
            flex-direction: column;
  }

  .why-card {
    margin: 10px 0;
    width: 90%;
  }
}

@media screen and (min-width: 768px) {
  /*768px is the difference between phone and tablet generally*/
  .why-card {
    height: 400px;
    width: 27%;
    margin: 5px;
  }
}

@media (orientation: landscape) {
  .why-card {
    height: 400px;
  }
}

.form-wrapper {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-align-items: center;
          align-items: center;
  background: #52d0ff99;
  text-align: center;
  font-weight: lighter;
  font-size: 3vw;
  padding: 4%;
}

.form-title {
  font-size: 3rem;
}

.form-inputs {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: row;
          flex-direction: row;
  -webkit-justify-content: center;
          justify-content: center;
  width: 50%;
  margin: 20px;
}

.form-inputs input {
  border-radius: 3px;
  border: 1px solid #000000;
  font-size: 0.6em;
  padding: 10px;
}

.email-input {
  width: 70%;
}

.signup-button {
  margin: 0 2px;
  width: 30%;
  background: #000000;
  color: #fff;
  -webkit-appearance: none;
  -moz-appearance: none;
  appearance: none;
}

.signup-button:hover {
  color: #fff;
  background: #ffa29c;
}

@media screen and (max-width: 767px) {
  /*768px is the difference between phone and tablet generally*/
  .form-inputs {
    width: 80%;
  }
}

@media screen and (min-width: 768px) {
  /*768px is the difference between phone and tablet generally*/
  .form-inputs {
    width: 90%;
  }
}

@media (orientation: landscape) {
  .form-inputs {
    width: 90%;
  }
}

